body {
    padding-top: 125px;
}
header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 1;
    background-color: $white;
    @include box-shadow(0 0 20px -8px rgba(0,0,0,.1))

    .container {
        @include responsive(ipad-portrait, max) {
            padding: 0;
        }

        .row {
            @include responsive(mobile-small, max) {
                margin: 0;
            }
        }
    }

    // Header logo
    .main-logo {

        @include responsive(ipad-portrait, max) {
            margin-bottom: 30px;
        }
        @include responsive(mobile-big, max) {
            margin-bottom: 0;
        }

        img {
            width: auto;
            height: 67px;
            
            @include responsive(mobile-small, max) {
                height: 30px;
            }
        }
    }

    // Header Button
    .header-btn {
        .search-icon {
            width: 18px;
            margin-left: 20px;
        }

        .btn {
            padding: 7px 24px;
            font-size: 15px;
            line-height: 16px;
            font-weight: 400;
            margin-left: 10px;
        }
    }

    // Navigation
    nav.navbar {
        padding: 0;

        @include responsive(mobile-big, max) {
        }

        .navbar-nav {
            @media (min-width: 768px) {
                flex-direction: row;
            }

            > .menu-item {
                margin-right: 6px;
                position: relative;

                @include responsive(mobile-big, max) {
                    margin: 0;
                    border-bottom: 1px solid $white;
                }

                &:last-child {
                    margin: 0;

                    @include responsive(mobile-big, max) {
                        border: none;
                    }
                }

                > a {
                    color: $purple;
                    font-size: 15px;
                    line-height: 25px;
                    font-weight: 600;
                    padding: 50px 10px;
                    text-decoration: none;
                }
                
                &:hover {
                    .mega-menu-wrapper {
                        opacity: 1;
                        visibility: visible;
                    }
                }
            }

            // Mega Menu Wrapper
            .mega-menu-wrapper {
                display: none;
            }
        }
    }

    // navbar toggle
    .navbar-toggler {
        width: 26px;
        height: 30px;
        padding: 0;
        position: absolute;
        top: 26px;
        left: 35px;
        display: inline-flex;
        @include align-items(center);

        @include responsive(mobile-small, max) {
            left: 20px;
        }

        &:hover,
        &:focus {
            outline: none;
            box-shadow: none;
            border: none;
            background: transparent;
        }

        .navbar-toggler-line {
            width: 100%;
            height: 5px;
            background-color: $black;
            position: relative;
            @include borderRadius(3px);
            @include transitionAll(0.3s ease-in-out);
            
            &:before,
            &:after {
                content: "";
                width: 100%;
                height: 100%;
                background-color: $black;
                position: absolute;
                left: 0;
                @include borderRadius(3px);
                @include transitionAll(0.3s ease-in-out);
            }

            &:before {
                top: -10px;
            }

            &:after {
                top: 10px;
            }
        }

        &:not(.collapsed) {
            .navbar-toggler-line {
                background-color: transparent;

                &:before {
                    top: 0;
                    @include rotate(45deg);
                }

                &:after {
                    top: 0;
                    @include rotate(-45deg);
                }
            }
        }
    }

    // mobile menu
    .navbar-collapse {
        @include responsive(mobile-big, max) {
            position: absolute;
            padding: 0 15px;
            top: 83px;
            left: 0;
            width: 60%;
            z-index: 9;
            height: calc(100vh - 83px);
            overflow-y: auto;
            @include transitionAll(0.5s);
        }
        
        @include responsive(mobile-small, max) {
            width: 70%;
            top: 80px;
            height: calc(100vh - 80px);
        }

        &.collapse:not(.show) {
            @include responsive(mobile-big, max) {
                display: block;
                left: -100%;
            }
        }
    }

    // Header Center
    .header-center {
        padding: 70px 0;
        border-top: 1px solid #eeeeee;
    }
}
